import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FaGithub, FaLinkedin, FaTwitter, FaDownload, FaSpinner, FaCheck, FaEnvelope } from 'react-icons/fa';
import useTypewriter from '../hooks/useTypewriter';
import { useSoundEffect } from '../hooks/useSoundEffect';
import jsPDF from 'jspdf';
import '../styles/animations.css';
import { SocialIcon } from 'react-social-icons';

const ProfileCard = () => {
  const [isFlipped, setIsFlipped] = useState(false);

  return (
    <motion.div
      className="relative w-48 h-48 perspective-1000"
      whileHover={{ scale: 1.02 }}
      onHoverStart={() => setIsFlipped(true)}
      onHoverEnd={() => setIsFlipped(false)}
    >
      <motion.div
        className="w-full h-full relative transition-transform duration-500 transform-style-3d"
        animate={{ rotateY: isFlipped ? 180 : 0 }}
      >
        {/* Front of card */}
        <div className="absolute w-full h-full backface-hidden">
          <div className="rounded-full overflow-hidden gradient-border p-1">
            <img 
              src="/profile.jpg" 
              alt="Profile"
              className="rounded-full w-full h-full object-cover"
            />
          </div>
        </div>
        
        {/* Back of card */}
        <div className="absolute w-full h-full backface-hidden transform rotate-y-180 bg-darker rounded-full p-6 flex items-center justify-center text-center">
          <p className="text-sm">
            Passionate about creating beautiful and functional web experiences
          </p>
        </div>
      </motion.div>
    </motion.div>
  );
};

const Hero = () => {
  const roles = ["Web Developer", "Software Engineer", "SZABIST Student"];
  const displayText = useTypewriter(roles, {
    typingSpeed: 100,
    deletingSpeed: 50,
    pauseTime: 2000
  });
  const playSound = useSoundEffect();
  const [isDownloading, setIsDownloading] = React.useState(false);

  const generatePDF = () => {
    setIsDownloading(true);
    const doc = new jsPDF();
    doc.text("John Doe - Resume", 20, 10);
    // Add more content to PDF
    doc.save("john-doe-resume.pdf");
    playSound('click');
    setTimeout(() => setIsDownloading(false), 2000);
  };

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.3,
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.5,
      }
    }
  };

  const socialLinks = [
    { icon: <FaGithub size={24} />, url: 'https://github.com' },
    { icon: <FaLinkedin size={24} />, url: 'https://linkedin.com' },
    { icon: <FaTwitter size={24} />, url: 'https://twitter.com' }
  ];

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ 
        behavior: 'smooth',
        block: 'start'
      });
    }
  };

  return (
    <motion.section
      variants={containerVariants}
      initial="hidden"
      animate="visible"
      className="relative max-w-7xl mx-auto px-8 py-12 bg-darker/50 backdrop-blur-sm rounded-2xl shadow-xl overflow-hidden"
    >
      <div className="light-sweep" />
      
      <div className="flex flex-col md:flex-row items-center md:items-start gap-12">
        {/* Left Column - Profile Card */}
        <motion.div variants={itemVariants} className="flex-shrink-0">
          <ProfileCard />
        </motion.div>

        {/* Right Column - Content */}
        <div className="flex-grow">
          <motion.div variants={itemVariants} className="text-primary font-medium mb-4">
            Hey there <span className="wave-animation inline-block">ðŸ‘‹</span>
          </motion.div>
          
          <motion.h1 
            variants={itemVariants}
            className="text-5xl md:text-6xl font-bold mb-4 font-poppins tracking-tight
              text-transparent bg-clip-text bg-gradient-to-r from-white to-gray-300"
          >
            Fahim Ahmad
          </motion.h1>

          <motion.h2 
            variants={itemVariants}
            className="text-2xl md:text-3xl text-gray-400 mb-6 h-[40px] font-poppins"
          >
            {displayText}
            <span className="animate-pulse">|</span>
          </motion.h2>

          <motion.p 
            variants={itemVariants}
            className="text-gray-400 text-lg mb-8 max-w-2xl leading-relaxed"
          >
            A passionate Web Developer with 3+ years of experience, currently pursuing
            my degree at SZABIST Karachi. Specialized in modern web technologies and
            software development practices, with a strong foundation in computer science fundamentals.
          </motion.p>

          {/* Buttons Row */}
          <motion.div
            variants={itemVariants}
            className="flex flex-wrap gap-4 mb-8"
          >
            <motion.button
              whileHover={{ scale: 1.05, translateY: -2 }}
              whileTap={{ scale: 0.95 }}
              className="bg-primary hover:bg-primary-light text-white px-6 py-3 
                rounded-lg flex items-center gap-2 transition-all duration-300
                hover:shadow-glow transform perspective-1000"
              onClick={generatePDF}
            >
              {isDownloading ? <FaSpinner className="animate-spin" /> : <FaDownload />}
              Download CV
            </motion.button>

            <motion.button
              whileHover={{ scale: 1.05, translateY: -2 }}
              whileTap={{ scale: 0.95 }}
              className="border-2 border-primary hover:bg-primary/10 text-primary 
                px-6 py-3 rounded-lg flex items-center gap-2 transition-all duration-300"
              onClick={() => scrollToSection('contact')}
            >
              <FaEnvelope />
              Contact Me
            </motion.button>
          </motion.div>

          {/* Social Links */}
          <motion.div 
            variants={itemVariants}
            className="flex items-center gap-6"
          >
            {socialLinks.map((link, index) => (
              <motion.a
                key={index}
                href={link.url}
                target="_blank"
                rel="noopener noreferrer"
                className="relative group"
                whileHover={{ scale: 1.1, y: -2 }}
                whileTap={{ scale: 0.95 }}
              >
                <span className="text-gray-400 hover:text-primary transition-colors duration-300">
                  {link.icon}
                </span>
                <span className="absolute -top-8 left-1/2 -translate-x-1/2 px-2 py-1 
                  bg-darker text-sm text-white rounded opacity-0 group-hover:opacity-100
                  transition-opacity duration-300"
                >
                  {link.label}
                </span>
              </motion.a>
            ))}
          </motion.div>
        </div>
      </div>

      {/* Scroll Indicator */}
      <motion.div
        variants={itemVariants}
        className="absolute bottom-8 left-1/2 -translate-x-1/2 cursor-pointer
          opacity-50 hover:opacity-100 transition-opacity duration-300"
        onClick={() => scrollToSection('about')}
      >
        <div className="w-6 h-10 border-2 border-white rounded-full p-1">
          <motion.div 
            className="w-2 h-2 bg-white rounded-full mx-auto"
            animate={{ y: [0, 12, 0] }}
            transition={{
              duration: 1.5,
              repeat: Infinity,
              ease: "easeInOut"
            }}
          />
        </div>
      </motion.div>
    </motion.section>
  );
};

const DownloadButton = ({ generatePDF, isDownloading }) => {
  const [showCheckmark, setShowCheckmark] = useState(false);

  const handleClick = async () => {
    generatePDF();
    await new Promise(resolve => setTimeout(resolve, 2000));
    setShowCheckmark(true);
    setTimeout(() => setShowCheckmark(false), 1500);
  };

  return (
    <motion.button
      onClick={handleClick}
      className="relative bg-gradient-to-r from-primary to-primary-light text-white 
        px-8 py-4 rounded-lg flex items-center gap-2 overflow-hidden group"
      whileHover={{ scale: 1.05 }}
      whileTap={{ scale: 0.95 }}
    >
      <AnimatePresence mode="wait">
        {isDownloading ? (
          <motion.div
            key="loading"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="animate-spin"
          >
            <FaSpinner />
          </motion.div>
        ) : showCheckmark ? (
          <motion.div
            key="success"
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            exit={{ scale: 0 }}
          >
            <FaCheck />
          </motion.div>
        ) : (
          <FaDownload className="group-hover:animate-bounce" />
        )}
      </AnimatePresence>
      {isDownloading ? "Downloading..." : "Download CV"}
    </motion.button>
  );
};

export default Hero;
