import { useState, useEffect, useCallback } from 'react';

const useTypewriter = (strings, options = {}) => {
  const {
    typingSpeed = 100,
    deletingSpeed = 50,
    pauseTime = 1500,
  } = options;

  const [displayText, setDisplayText] = useState('');
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isTyping, setIsTyping] = useState(true);

  const typeString = useCallback(() => {
    const currentString = strings[currentIndex];
    
    if (isTyping) {
      if (displayText.length < currentString.length) {
        setDisplayText(prev => currentString.slice(0, prev.length + 1));
      } else {
        setIsTyping(false);
        setTimeout(() => {
          setIsTyping(false);
        }, pauseTime);
      }
    } else {
      if (displayText.length > 0) {
        setDisplayText(prev => prev.slice(0, -1));
      } else {
        setIsTyping(true);
        setCurrentIndex((prev) => (prev + 1) % strings.length);
      }
    }
  }, [currentIndex, displayText, isTyping, strings, pauseTime]);

  useEffect(() => {
    const timer = setTimeout(
      typeString,
      isTyping ? typingSpeed : deletingSpeed
    );
    return () => clearTimeout(timer);
  }, [typeString, isTyping, typingSpeed, deletingSpeed]);

  return displayText;
};

export default useTypewriter;
